import React, { useState, useEffect } from "react";
import "./city-switcher.scss";
import Select from "react-select";
import Budget from "components/budget";

const CitySwitcher = ({ data }) => {
  const dataByCity = selectData(data);
  const cityOptions = Object.keys(dataByCity).map((d) => ({
    value: d,
    label: d,
  }));

  const [city, setCity] = useState(cityOptions[0].value);

  useEffect(() => {
    const cityValues = cityOptions.map(({ value }) => value);
    const nextCity = () => {
      const index = cityValues.indexOf(city);
      const next = cityOptions[index + 1]
        ? cityOptions[index + 1]
        : cityOptions[0];
      setCity(next.value);
    };
    const prevCity = () => {
      const index = cityValues.indexOf(city);
      const prev = cityOptions[index - 1]
        ? cityOptions[index - 1]
        : cityOptions[cityValues.length - 1];
      setCity(prev.value);
    };
    const onKeyPress = (e) => {
      console.log(e.key);
      if (e.key === "ArrowRight") {
        nextCity();
      } else if (e.key === "ArrowLeft") {
        prevCity();
      }
    };
    window.addEventListener("keydown", onKeyPress);
    return () => window.removeEventListener("keydown", onKeyPress);
  }, [city, cityOptions]);

  return (
    <div className="CitySwitcher">
      <Select
        options={cityOptions}
        value={cityOptions.find(({ value }) => value === city)}
        onChange={(value) => {
          setCity(value.value);
        }}
      />
      <Budget city={city} data={dataByCity[city]} />
    </div>
  );
};

export default CitySwitcher;

const selectData = (data) => {
  const dataByCity = data.reduce((r, d) => {
    if (!r[d["citystate"]]) {
      r[d["citystate"]] = [];
    }
    r[d["citystate"]].push(d);
    return r;
  }, {});

  return organizeBudget(dataByCity);
};

const organizeBudget = (data) => {
  return Object.keys(data).reduce((r, city) => {
    r[city] = groupByKey("department", data[city]);
    Object.keys(r[city]).forEach((department) => {
      r[city][department] = groupByKey("expensecat", r[city][department]);

      Object.keys(r[city][department]).forEach((category) => {
        r[city][department][category] = groupByKey(
          "expensedescrip",
          r[city][department][category]
        );

        Object.keys(r[city][department][category]).forEach((description) => {
          r[city][department][category][description] = groupByKey(
            "personneltype",
            r[city][department][category][description]
          );
        });
      });
    });
    return r;
  }, {});
};

const groupByKey = (key, rows) => {
  return rows.reduce((r, d) => {
    if (!r[d[key]]) {
      r[d[key]] = [];
    }
    r[d[key]].push(d);
    return r;
  }, {});
};
